.button {
  width: 100%;
  height: 50px;
  padding-top: 13px;
  padding-bottom: 13px;
  background-color: #24cca7;
  font-family: 'CirceRegular';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  text-align: center;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: white;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 400ms;
}

.button:hover,
.button:focus {
  background-color: #21bd9b;
}

@media screen and (min-width: 479px) {
  .button {
    width: 300px;
  }
}
